h1{
  text-align: left;
  color: #081E3F;
  background-color: #F8C93E;
  padding: 20px;
  margin-bottom: 0px;
  position: relative;
  top: 0;
  left: 0;
  z-index: 1;
}
h3{
  text-align: center;
  color: #081E3F;
  background-color: #F8C93E;
  padding: 10px;
}

.App {
  text-align: center;
  color:#081E3F;
  margin-left: auto;
  margin-right: auto;
  background: #081E3F;
  position: relative;
    width: 100%;
    height: 100%;
    left: 0;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}